.NavBar .liBar {
  list-style-type: none;
  cursor: pointer;
  margin-top: 1rem;
  margin-bottom: 6rem;
}
.NavBar .liBar a {
  color: white;
  font-size: 4rem;
  text-decoration: none;
  position: relative;
}
.NavBar .liBar a:after {
  content: "";
  width: 0%;
  height: 0.2rem;
  position: absolute;
  background: #00b4db;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3acfd5 0%, #3a4ed5);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  bottom: 0%;
  left: 50%;
  border-radius: 10px;
  transition: all 400ms ease;
}
.NavBar .liBar a:hover {
  color: #1b83bd;
  padding-bottom: 0.6rem;
}
.NavBar .liBar a:hover::after {
  left: 0%;
  width: 100%;
}

.notArrow {
  display: none;
}

.arrow {
  position: fixed;
  display: inline;
  top: 90vh;
  left: 90vw;
  font-size: 3rem;
  z-index: 5;
  -webkit-animation-name: myArrow;
  /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 4s;
  /* Safari 4.0 - 8.0 */
  -webkit-animation-iteration-count: 7;
  animation-name: myArrow;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  cursor: pointer;
}

@-webkit-keyframes myArrow {
  0% {
    top: 90vh;
  }
  25% {
    top: 89.8vh;
  }
  50% {
    top: 89.6vh;
  }
  75% {
    top: 89.8vh;
  }
  100% {
    top: 90vh;
  }
}

@keyframes myArrow {
  0% {
    top: 90vh;
  }
  25% {
    top: 89.8vh;
  }
  50% {
    top: 89.6vh;
  }
  75% {
    top: 89.8vh;
  }
  100% {
    top: 90vh;
  }
}
